___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: #a9957b;
}

header {
  background-color: #bdaa93;
  margin-top: 0px;
}

nav {
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
nav button {
  height: 60px;
  width: 90px;
  border-radius: 10px;
  font-weight: bold;
}

#content {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}
.headline {
  margin-bottom: 20px;
  text-align: center;
  font-size: 200%;
}
.image {
  height: 300px;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  height: 300px;
  width: 500px;
}
.description {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.description div {
  width: 700px;
  font-size: 150%;
  text-align: center;
}

.highlight {
  font-style: italic;
  font-weight: 700;
}

div.menu {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 200%;
}
ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

div.about {
  display: flex;
  flex-direction: column;
  font-size: 150%;
}
p {
  text-align: center;
}
